import {AnimatePresence, motion,   useMotionValue,
  useTransform,
} from "framer-motion"
import { useRef, useState } from "react"

export default function Todo({colorselected, task,selectedEmoji, category, additionalInfo} : {colorselected:string, selectedEmoji: string, task: string, category: string, additionalInfo: string}) {


  const [start, setstart] = useState(0)
  const x = useMotionValue(0)
  const item = useRef<HTMLDivElement>(null)
  const opacity = useTransform(x, [-100, 0, 100], [0, 1, 0])



  return (
    <motion.div  ref={item} drag style={{ x, opacity }} layout transition={{ duration: 0.3 }} onDragEnd={
      (event, info) => {
      if (start < (info.point.x - start) || (info.point.x - start) < -500) {
        console.log("test");
        if (item.current !== null) {
          console.log(task);
          const array = localStorage.getItem("todos")
          if (array !== null) {
            const parsedArray = JSON.parse(array);
           const newarr = parsedArray.filter((todo: { task: string }) => todo.task !== task);
           localStorage.setItem("todos", JSON.stringify(newarr))
           item.current.remove()
          }          
          // item.current.remove()
        }
      }}
    } onDragStart={
      (event, info) =>{ setstart(prev => info.point.x)}
    }  onDrag={
      (event, info) => console.log(info.point.x, info.point.y)
    } whileDrag={{ scale: 1.2 }} dragElastic={0.2} dragConstraints={{ left: 0, right: 0, top: 0, bottom: 0 }} className={"flex cursor-pointer flex-col min-w-full bg-white bg-opacity-5 ring-2 ring-white ring-opacity-10 rounded-sm px-4 py-2 " + colorselected}>
        <p className=" text-sm text-white text-opacity-50 font-light">{category}</p>
        <h2 className="text-xl text-white text-opacity-90 font-bold">{task} <span>{selectedEmoji}</span></h2>
        <p className="text-md min-w-full text-white text-opacity-75 font-normal">{additionalInfo}</p>
    </motion.div>
  )
}
