import { useState, ChangeEvent } from "react";
import {motion} from "framer-motion"
interface InputProps {
  type: "text" | "password" | "textarea";
  name?: string;
  className?: string;
  placeHolder: string;
  autoFocus?: boolean;
  value: string;
  onChange?: (value: string) => void;
  onChange2?: (value: string) => void;
}

export default function Input({
  type,
  name,
  className,
  placeHolder,
  autoFocus,
  value,
  onChange,
  onChange2
}: InputProps) {
  const [isOvered, setisOvered] = useState(false);
  const [valuee, setvaluee] = useState("");

  const handleValue = (e: ChangeEvent<HTMLInputElement>) => {
    const inputValue = e.target.value;
    onChange && onChange(inputValue);
    setvaluee(inputValue);
    if (inputValue) {
      setisOvered(true);
    }
  };

  const handleValue2 = (e: ChangeEvent<HTMLTextAreaElement>) => {
    const inputValue = e.target.value;
    onChange2 && onChange2(inputValue);
    setvaluee(inputValue);
    if (inputValue) {
      setisOvered(true);
    }
  };

  const handleFocus = () => {
    setisOvered(true);
  };

  const handleBlur = () => {
    if (!valuee) {
      setisOvered(false);
    }
  };

  return (
    <div className="flex justify-center items-center relative">
      {type === "textarea" ? (
        <motion.textarea
         layoutScroll
          wrap="soft"
          autoFocus={autoFocus}
          onBlur={handleBlur}
          onFocus={handleFocus}
          name={name}
          value={value}
          onChange={handleValue2}
          className={`bg-transparent w-full focus:outline-none ring-1 px-4 py-4 ring-slate-50 ring-opacity-10 focus:ring-emerald-300 rounded-md transition-shadow ${className}`}
        />
      ) : (
        <input
          autoFocus={autoFocus}
          onBlur={handleBlur}
          onFocus={handleFocus}
          type={type}
          name={name}
          value={value}
          onChange={handleValue}
          className={`bg-transparent w-full focus:outline-none ring-1 px-4 py-2 ring-slate-50 ring-opacity-10 focus:ring-emerald-300 rounded-md transition-shadow ${className}`}
        />
      )}
      


<motion.label
  htmlFor=""
  transition={{duration: .1}}
  initial={{ transform: 'scale(1)', top: '7px'}}
  animate={{
    transform: isOvered ? 'scale(0.8)' : 'scale(1)',
    top: isOvered ? '-10px' : '7px',
    left: isOvered ? '0px' : "10px"
  }}
  className="absolute left-4 bg-slate-950 px-2 text-white text-opacity-30 select-none pointer-events-none transition-all"
>
  {placeHolder}
</motion.label>
    </div>
  );
}
