import Todo from "./Todo";
import { useEffect, useState } from "react";
import { LayoutGroup, motion } from "framer-motion";

interface TodoItem {
    task: string;
  selectedEmoji: string;
  category: string;
  additionalInfo: string;
  colorselected: string;
}

export default function Todos() {
  const [todos, setTodos] = useState<TodoItem[]>([]);

  useEffect(() => {
    
    const todosFromLocalStorage = localStorage.getItem("todos");
    if (todosFromLocalStorage) {
      const parsedTodos: TodoItem[] = JSON.parse(todosFromLocalStorage);
      console.log(parsedTodos);
      setTodos(parsedTodos);
    }
  }, []);

  return (
      <motion.div className="min-w-[300px] max-w-[500px] flex justify-center items-center gap-4 flex-col" >
        {todos ? todos.map((todo:TodoItem) => (
          <Todo colorselected={todo.colorselected} key={todo.task} task={todo.task} selectedEmoji={todo.selectedEmoji} additionalInfo={todo.additionalInfo} category={todo.category} />
        )): ""}
      </motion.div>
  );
}
