import { useState , MouseEvent  } from "react";
import Preview from "../Preview";
import Input from "./Input";
import Picker from '@emoji-mart/react'
import data from '@emoji-mart/data'
import {motion} from "framer-motion"
import { Link, useNavigate } from 'react-router-dom';
import Error from "../Error";
let timer: NodeJS.Timeout;


export default function Modal() {
  const navigate = useNavigate()
  const [task, setTask] = useState("");
  const [category, setCategory] = useState("");
  const [additionalInfo, setAdditionalInfo] = useState("");
  const [issmile, setissmile] = useState(false)
  const [selectedEmoji, setSelectedEmoji] = useState("");
  const [err, seterr] = useState(false);
  const [colorselected, setcolorselected] = useState("")
  const openSmile = () => {
    setTimeout(() => {
      setissmile(!issmile)
    }, 1);
  }
  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!task) {
      seterr(true)
      if (timer) {
        clearTimeout(timer); // Rinitialiser le timer prcdent
      }
      timer = setTimeout(function() {
      seterr(false)
      }, 2000);
      return
    }    
    console.log(task, category, additionalInfo, selectedEmoji, colorselected);
    const todo = {
      task,
      category,
      additionalInfo,
      selectedEmoji,
      colorselected
    };
    const todosFromLocalStorage = localStorage.getItem("todos");
    const todos = todosFromLocalStorage ? JSON.parse(todosFromLocalStorage) : [];  
    
    todos.push(todo)
    
    localStorage.setItem("todos", JSON.stringify(todos));
    navigate("/")
  };
  
  const handleColor = (color: string) => {
    console.log(color);
    setcolorselected(color)
  };
  

  const handleTaskChange =  (value: string) => {
    setTask(value);
  };

  const handleCategoryChange =  (value: string) => {
    setCategory(value);
  };

  const handleAdditionalInfoChange = (value: string) => {
    setAdditionalInfo(value);
  };

  const handleEmojiClick = (emoji: any) => {
    setSelectedEmoji(emoji.native);
    setissmile(false)
    console.log(emoji);
  };
  return (
    <div className="bg-slate-950 flex h-screen justify-center items-center text-white">
      {err && <Error></Error>}
       <motion.div  initial={{ opacity: 0, scale: .95, translateY: -20 }}
    animate={{ opacity: 1, scale: 1, translateY: 0  }}
    exit={{ opacity: 0, scale: .95, translateY: -20  }}
    transition={{type: "spring", duration: .25}}
    className="flex">
      <form className="flex flex-col gap-4"  onSubmit={handleSubmit}>
        <Input
          type="text"
          className=""
          placeHolder="Task"
          autoFocus={true}
          value={task}
          onChange={handleTaskChange}
        />
        <Input
          type="text"
          className=""
          placeHolder="Catgorie"
          value={category}
          onChange={handleCategoryChange}
        />
        <Input
          type="textarea"
          className=""
          placeHolder="Plus d'infos"
          value={additionalInfo}
          onChange2={handleAdditionalInfoChange}
        />
        <div className="flex items-center gap-4 relative">
        <span onClick={openSmile} className="p-4 select-none bg-white bg-opacity-5 ring-1 ring-white ring-opacity-10 w-fit cursor-pointer">{selectedEmoji}</span>
        {issmile &&   
        <div className="fixed z-50">
          <Picker
          data={data}
            onEmojiSelect={handleEmojiClick}
            skin={2}
            onClickOutside={() => setissmile(false)}
          />
        </div>
        }
          <button type="submit" className="flex w-full h-full justify-center items-center ring-1 ring-white hover:ring-opacity-100 outline-none hover:ring-emerald-300 ring-opacity-10">Add Todo</button>
        </div>
      </form>
      <Preview colorselected={colorselected} handleColor={handleColor} selectedEmoji={selectedEmoji} task={task} category={category} additionalInfo={additionalInfo} />
    </motion.div>
    <p className='text-sm fixed bottom-0 right-0 py-4 px-8'>
        Todo make with  by <Link className='text-emerald-300 bg-white bg-opacity-5 rounded-lg ring-white ring-opacity-10 ring-1 p-1  font-semibold' target='blank' to="https://github.com/JulienSuan">Julien Bigot</Link>
      </p>
    </div>
   
  );
}
