import {motion} from "framer-motion"
import "./Preview.css"

const tab = ["default", "item", "item2", "item3", "item4"]


export default function Preview({handleColor, task,selectedEmoji, category, additionalInfo, colorselected} : {colorselected:string, handleColor: (color: string) => void, selectedEmoji: string, task: string, category: string, additionalInfo: string}) {
  return (
    <div className={"flex w-[500px] relative justify-center items-center flex-col ml-8 bg-white bg-opacity-5 p-8 rounded-lg "}>
      <h2 className="mb-4 absolute top-4 left-8">Task Preview</h2>
      <motion.div layout transition={{ duration: 0.3 }} className={"flex flex-col min-w-full bg-white bg-opacity-5 ring-2 ring-white ring-opacity-10 rounded-sm px-4 py-2 "  + colorselected}>
        <p className=" text-sm text-white overflow-wrap break-word text-opacity-50 font-light">{category || task || additionalInfo ? category : "Travail"}</p>
        <h2 className="text-xl text-white  overflow-wrap break-wordtext-opacity-90 font-bold">{category || task || additionalInfo ? task : "Apprendre NextJs"} <span>{selectedEmoji}</span></h2>
        <p className="text-md min-w-full text-white overflow-wrap break-word text-opacity-75 font-normal">{category || task || additionalInfo ?  additionalInfo : "De 8h  12h apprendre NextJS, ps: ne pas oublier de regarder les docs"}</p>
      </motion.div>
        <div className="flex gap-2">
          {tab.map((color, index) => {
            return (
              <div key={index} onClick={() => handleColor(color)} className={`${color}_lol hover:scale-105 transition-all hover:ring-2 ring-opacity-90 ring-white w-8 h-8 rounded-lg cursor-pointer mt-4`}></div>
            )
          })}
        </div>
    </div>
  )
}
